.stream-tile {
  background-color: #1e1e1e;
  border-radius: 8px;
  overflow: hidden;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.stream-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-grow: 1;
}

.video-container {
  position: relative;
  flex-grow: 1;
  height: 100%;
  min-height: 100px;
  background-color: #000;
}

.video-container iframe, 
.video-container video {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.audio-levels {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: calc(100% - 60px);
  display: flex;
  flex-direction: column-reverse;
  justify-content: flex-start;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  padding: 2px;
}

.level-bar {
  width: 100%;
  margin-bottom: 1px;
  transition: height 0.05s ease-out;
  min-height: 2px;
}

.stream-controls {
  position: absolute;
  bottom: 10px;
  left: 10px;
  display: flex;
  gap: 10px;
  z-index: 10;
}

.stream-controls button {
  background: rgba(0, 0, 0, 0.7);
  border: none;
  color: white;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.2s;
}

.stream-controls button:hover {
  background: rgba(0, 0, 0, 0.9);
  transform: scale(1.1);
}

.audio-toggle {
  font-size: 18px;
}

.remove-stream {
  font-size: 14px;
}

.stream-info {
  padding: 8px;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.stream-type {
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
  opacity: 0.7;
}

.loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
}

.error-message {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(100, 0, 0, 0.7);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  text-align: center;
  padding: 20px;
}
